	module.exports = {"data":{"post":{"id":"/Users/Vikas/learning/my_github_page/content/posts/2018-06-10--IoT-dashboard-energy-monitoring/index.md absPath of file >>> MarkdownRemark","html":"<p>A few months back I started working on the IoT Dashboard project, to visualize the data I have been gathering from my Energy meter installed at home. The energy meter is based on Atmega328 controller and sends data periodically to the server over GPRS network. It was build using the <a href=\"https://github.com/vkylamba/openiotboard\">Open IoT Development Board</a>.</p>\n<p>…</p>\n<h4>Setting up IoT Dashboard</h4>\n<p>Let’s start with creating an account on <a href=\"https://data.okosengineering.com/user/login\">IoT Dashboard</a>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/signup-5683dc46db98ec483fca393e1df60598-7119c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 700px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 160.85714285714286%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"signup\"\n        title=\"\"\n        src=\"/static/signup-5683dc46db98ec483fca393e1df60598-7119c.png\"\n        srcset=\"/static/signup-5683dc46db98ec483fca393e1df60598-155a8.png 200w,\n/static/signup-5683dc46db98ec483fca393e1df60598-38447.png 400w,\n/static/signup-5683dc46db98ec483fca393e1df60598-7119c.png 700w\"\n        sizes=\"(max-width: 700px) 100vw, 700px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>We are presented with the following page on signup.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/dashboard-de95d445ebdf47fdab301d52abd4cc0e-629ce.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 57.301808066759385%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"dashboard\"\n        title=\"\"\n        src=\"/static/dashboard-de95d445ebdf47fdab301d52abd4cc0e-48538.png\"\n        srcset=\"/static/dashboard-de95d445ebdf47fdab301d52abd4cc0e-bed0f.png 200w,\n/static/dashboard-de95d445ebdf47fdab301d52abd4cc0e-5fd34.png 400w,\n/static/dashboard-de95d445ebdf47fdab301d52abd4cc0e-48538.png 800w,\n/static/dashboard-de95d445ebdf47fdab301d52abd4cc0e-b70ec.png 1200w,\n/static/dashboard-de95d445ebdf47fdab301d52abd4cc0e-d134a.png 1600w,\n/static/dashboard-de95d445ebdf47fdab301d52abd4cc0e-f6ead.png 2400w,\n/static/dashboard-de95d445ebdf47fdab301d52abd4cc0e-629ce.png 2876w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Lets add a new device now. Select the device type to be “Home”, Give a name to the device, and put the address. Once your device is created, it gets listed on the left side bar. Click on the device and it should show the API URL, and API key where your device can dump the data. An example of data point is also given.</p>\n<p>…</p>","fields":{"slug":"/IoT-dashboard-energy-monitoring/","prefix":"2018-06-10"},"frontmatter":{"title":"Home energy monitoring with IoT Dashboard","subTitle":"Energy Analysis","cover":{"childImageSharp":{"resize":{"src":"/static/homepage-3ccfb029b4c9a836cd03c4556b95f0ec-160fa.png"}}}}},"author":{"id":"/Users/Vikas/learning/my_github_page/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Vikas Lamba</strong> </p>"},"footnote":{"id":"/Users/Vikas/learning/my_github_page/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>GitHub</li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"..."}}}},"pathContext":{"slug":"/IoT-dashboard-energy-monitoring/"}}

